  verqr()
  {
    this.options = {
      prompt : "Esperando codigo...",
      preferFrontCamera:false,
      showFlipCameraButton:true,
      showTorchButton:true
  }





    this.barcodeScanner.scan(this.options).then(barcodeData => {

      var codigo:string=barcodeData.text;



      var aar =codigo.split("=");
          if(codigo!='')
          {
               var aa:string=aar[2]
            if(Number(aa)>0)
            {

              this.regService.getRegS2(aa,'estudiantepcb').then((data) => {
                this.Pdfx.dapcb(aa,'si',data)
              }, (err) => {
                this.msj.showError3('Resultado','Resultado no encontrado.');
            });

            }
            else
            {
             this.regService.getReg2b(aa,'asignaestudiantes').then((data) => {
               //   this.Pdfx.daasigna(aa,'si',data,data[0].no_orientacion,'','')
                }, (err) => {
                  this.msj.showError3('Resultado','Resultado no encontrado.');
              });
            }

          }




    }, (err) => {
      this.msj.showError3('Error','ERRRO en lectura de codigo');
    });



  }
