  //  if(this.rol=='Adminsuncentro' || this.rol=='SUN OPE ADMIN' || this.rol=='PAP Y SUN OPE ADMIN' || this.rol=='Adminsuncentro' ||  this.rol=='Administrador' || this.rol=='SUN OPERADOR'  || this.rol=='SUN ADMINISTRADOR' || this.rol=='SUN OPE ADMIN')

